import { generateService } from "../../generateServices/generateService";
const fetchSchedules = async (
  courses,
  minNumberOfDays,
  startTime,
  endTime,
  days,
  pinnedSections
) => {
  const res = await fetch(`${process.env.REACT_APP_API_URL}/api/v1/generate`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      options: {
        courses,
        minNumberOfDays,
        startTime,
        endTime,
        days,
        pinnedSections,
      },
    }),
  });

  const results = await res.json();

  return results;
};
export const fetchSections = async (lineNumber) => {
  const data = await fetch(`/api/v1/sections/${lineNumber}`);
  const d = await data.json();
  d.sort((a, b) => a.number - b.number);
  return d;
};
const generateSchedules = async (
  courses,
  minNumberOfDays,
  startTime,
  endTime,
  days,
  pinnedSections
) => {
  const res = await generateService({
    courses,
    minNumberOfDays,
    startTime,
    endTime,
    days,
    pinnedSections,
  });
  return res;
};
/** 
 * 
 * generates schedules based on the device type
 * if the device is mobile or tablet, it will generate on the server schedules using the fetchSchedules function
 * otherwise, it will generate schedules on the client using the generateSchedules function
 * @returns schedules: an array of arrays of sections
*/
export const balanceScheduleGenerator = async (
  courses,
  minNumberOfDays,
  startTime,
  endTime,
  days,
  pinnedSections
) => {
  if (courses.length === 0) return [];
  if (mobileAndTabletCheck()){
    return await fetchSchedules(
      courses,
      minNumberOfDays,
      startTime,
      endTime,
      days,
      pinnedSections
    );
  }
  return await generateSchedules(
    courses,
    minNumberOfDays,
    startTime,
    endTime,
    days,
    pinnedSections
  );
};

